.container {
  display: flex;
  align-items: center;
}
.name {
  display: block;
  margin-right: 5px;
  margin-left: 5px;
  text-align: center;
  font-size: 15px;
}
.button {
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: none;
  background-color: black;
  color: white;
  cursor: pointer;
}
.button:hover {
  background-color: blue;
}
