.sectionNav {
  display: flex;
  width: 100%;
  height: 50px;
  justify-content: center;
  align-items: center;
  box-shadow: 0 5px 5px -5px rgba(0, 0, 0, 0.5);
  font-size: 20px;
  color: black;
}
.link {
  display: block;
  text-decoration: none;
  padding: 5px;
  border-radius: 20%;
  margin: 15px;
}
.link:hover {
  color: blue;
  background-color: darkorange;
}

.actLink {
  display: block;
  text-decoration: none;
  padding: 5px;
  border-radius: 20%;
  margin: 20px;
  color: blue;
}
